@import url(https://fonts.googleapis.com/css2?family=Lexend:wght@400;600&display=swap);
html {
	box-sizing: border-box;
	font-size: 16px;
}

*,
*:before,
*:after {
	box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
	margin: 0;
	padding: 0;
	font-weight: normal;
}

ol,
ul {
	list-style: none;
}

img {
	max-width: 100%;
	height: auto;
}



h1 {
	font-weight: 600;
}

h2 {
	font-size: 14px;
	font-weight: 600;
	margin-top: 4px;
}

h4 {
	text-transform: uppercase;
	font-weight: 600;
	color: #b9bdcf;
	font-size: 14px;
	margin-bottom: 24px;
	letter-spacing: 1px;
}



.App {
    padding: 120px;
    font-family: 'Lexend', sans-serif;
    font-weight: 400;
    background-color: #F6F7FB;
    max-width: 1600px;
    margin: 0 auto;
    height: 100%;
}
.Back {
	color: #1e86ff;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	grid-column: 1/3;
	margin-bottom: 142px;
}

.Back__arrow {
	width: 30px;
	margin-right: 12px;
}

.Back:hover {
	cursor: pointer;
}

.Banner {
	height: 180px;
	background-color: aquamarine;
	background-image: url(/github-jobs/static/media/backgroundImg.c8427568.png);
	background-position: center;
	background-size: cover;
	border-radius: 8px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	margin-bottom: 42px;
	grid-column: 1 / 3;
}

.Search {
	width: 65%;
}

.Search__form {
	display: -webkit-flex;
	display: flex;
	height: 55px;
	border-radius: 4px;
	background-color: white;
	padding: 4px;
}

.Search__input-container {
	-webkit-flex: 1 1;
	        flex: 1 1;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	padding: 0 12px;
}

.Search__input {
	color: #b9bdcf;
	font-size: 12px;
	width: 100%;
	border: none;
	margin-left: 10px;
	height: 100%;
}

.Search__btn {
	font-size: 15px;
	font-weight: bold;
	color: white;
	border: none;
	height: 100%;
	border-radius: 4px;
	width: 146px;
	background: #1e86ff;
}

.Search__btn:hover {
	cursor: pointer;
}

.Search__work-icon {
	height: 15px;
	width: 15px;
}



.JobCard {
	display: -webkit-flex;
	display: flex;
	padding: 12px;
	background-color: white;
	margin-bottom: 32px;
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
	border-radius: 4px;
	height: 114px;
	color: #334680;
}

.JobCard:hover {
	cursor: pointer;
}

.JobCard__primary-info {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	max-width: 323px;
}

.JobCard__secondary-info {
	margin-left: auto;
	display: -webkit-flex;
	display: flex;
	-webkit-align-self: end;
	        align-self: end;
}

.JobCard__secondary-info,
.JobCard__type {
	font-size: 12px;
}

.PageNav {
	display: -webkit-flex;
	display: flex;
	color: #b9bdcf;
	width: -webkit-max-content;
	width: max-content;
	margin-left: auto;
	grid-column: 2/3;
	-webkit-align-items: center;
	        align-items: center;
}

.PageNav > div {
	border-radius: 4px;
	border: 1px solid #b7bcce;
	width: 36px;
	height: 36px;
	margin-right: 12px;
}

.PageNav div:first-of-type {
	margin-left: 12px;
}

.PageNav > div:hover {
	color: #334680;
	border: 1px solid #334680;
}

.PageNav > *:hover {
	cursor: pointer;
}

.PageNav__number {
	font-size: 12px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

.PageNav__number.active {
	background-color: #1e86ff;
	color: white;
	font-weight: bold;
}

.Controller {
    display: grid;
    grid-template-columns: 1fr 2.8fr;
    grid-gap: 0 35px;
    gap: 0 35px;
    margin-top: 32px;
}
.FullTime {
    font-size: 14px;
}

.FullTime__checkbox {
    margin-right: 12px;
}
.LocationInput {
	font-size: 14px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin-top: 30px;
}

.LocationInput__input {
	border: none;
	height: 42px;
	width: 100%;
	font-size: 12px;
	color: #b9bdcf;
}

.LocationInput__input-container {
	background-color: white;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	border-radius: 4px;
	padding: 0 16px;
}

.LocationInput__globe-icon {
	height: 15px;
	width: 15px;
	margin-right: 10px;
}

.LocationInput__label {
	text-transform: uppercase;
	font-weight: bold;
	margin-bottom: 14px;
	color: #b9bdcf;
}

.Cities {
    margin-top: 24px;
}

.Cities > div {
    margin-bottom: 12px;
}

.Cities__input {
    margin-right: 6px;
}
.JobPage {
	display: grid;
	grid-template-columns: 1fr 2.2fr;
	grid-gap: 0 30px;
	gap: 0 30px;
}

.JobPage__company {
	display: -webkit-flex;
	display: flex;
}

.JobPage__description {
	margin-top: 35px;
	color: #334680;
	line-height: 24px;
	max-width: 730px;
}

.JobPage__description > h2,
.JobPage__description > h3,
.JobPage__description strong {
	margin: 24px 0;
}

.JobPage__contact p {
	line-height: 24px;
	max-width: 415px;
}

.JobPage__description strong {
	display: block;
}

.JobPage__header {
	grid-column: 1/3;
	margin-bottom: 65px;
}

.JobPage__title {
	font-size: 24px;
	color: #334680;
}

.JobPage__flex-grp {
	display: -webkit-flex;
	display: flex;
}

.JobPage__header-content {
	margin-bottom: 35px;
}

.JobPage__job-description {
	grid-column: 2/3;
}

.JobType {
	margin-top: auto;
	padding: 6px 8px;
	border: 1px solid #334680;
	color: #334680;
	border-radius: 4px;
	width: -webkit-max-content;
	width: max-content;
	font-size: 12px;
}
.JobType--JobPage {
	margin-left: 16px;
}

.PostDate {
	margin-top: auto;
	padding: 6px 8px;
	border-radius: 4px;
	width: -webkit-max-content;
	width: max-content;
	font-size: 12px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	color: #b9bdcf;
}

.PostDate__clock-icon {
	margin-right: 5px;
	height: 15px;
	width: 15px;
}
.PostDate--JobPage {
	padding: 0;
	margin-top: 8px;
}

.Location {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	margin-right: 20px;
	color: #b9bdcf;
    font-size: 12px;
}

.Location__globe-icon {
	margin-right: 5px;
	height: 15px;
	width: 15px;
}

.CompanyLogo {
	object-fit: contain;
	object-position: center;
	border-radius: 4px;
}

.CompanyLogo--reg {
	width: 90px;
	height: 90px;
	margin-right: 16px;
}

.CompanyLogo--sm {
	width: 42px;
	height: 42px;
	margin-right: 12px;
}
.CompanyName {
	color: #334680;
}

.CompanyName--sm {
	font-size: 14px;
}

.CompanyName--reg {
	font-size: 18px;
}



